.howitsmade {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-top: $dist60;
    padding-bottom: $dist60;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding-bottom: 64px;
  }

  @media screen and (min-width: 1200px) {
    padding-bottom: 120px;
  }

  background-color: getColor('bg-clr6');
  background-image: url('/src/images/How_its_made_png/milk-splash1x_320.png');
  background-repeat: no-repeat;
  background-position: top 574px right 1px;
  width: 100%;
}

.howitsmade_background {
  margin-bottom: 549px;
  z-index: 9;
}

.howitsmade__pretitle {
  @include pre-title;
  font-family: titan one, titan one;
  margin-bottom: 15px;
  color: getColor('bg-clr3');
  text-align: center;
}

.howitsmade__title {
  @include title;
  margin-bottom: 32px;
  font-family: titan one, titan one;
  color: getColor('main-clr');
  text-align: center;
}

.howitsmade__text--medium {
  @include font-size(12, 500, 1.62, null);
  color: inherit;
  padding-right: 60px;
  margin-bottom: 40px;
}

