.howitsmade__text--slim {
  @include font-size(12, 500, 1.62, null);
  color: getColor('font-clr1');
  padding-right: 60px;
}

.howitsmade__items:not(:last-child) {
  margin-bottom: 32px;
}

.howitsmade__components--quantity {
  font-family: titan one, titan one;
  @include font-size(36, 400, 1.14, 0.04em);
  color: getColor('main-clr');
  text-transform: uppercase;
}

.howitsmade__text--bold {
  @include font-size(12, 700, 1.7, null);
  padding-right: 50px;
}
